:root {
  --color-bg-dark: #eaedf2;
  --color-bg: #f7fafc;
  --color-grey: #476072;
  --color-text: #334257;
  --color-accent: #548CA8;
  --color-white: white;
  --color-scrollbar: #e2e8f2;
}

html.dark {
  --color-bg-dark: #1a1c35;
  --color-bg: #22243b;
  --color-grey: #4e4e4e;
  --color-text: #fdfffd;
}

* {
  box-sizing: border-box;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  accent-color: var(--color-accent);
  background: rgb(40,46,56);
  background: linear-gradient(90deg, rgba(40,46,56,1) 0%, rgba(146,167,195,1) 100%);
}
  #root {
  width: 100%;
  height: 60%;
  max-width: 500px;
  background-color: var(--color-bg-dark);
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-shadow: 10px 10px 10px -5px rgba(0,0,0,0.75);
-webkit-box-shadow: 10px 10px 10px -5px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 10px -5px rgba(0,0,0,0.75);
}

ul {
  list-style: none;
  padding-left: 0;
}

button {
  outline: none;
  border: none;
}

::-webkit-scrollbar {
  width: 0.5rem;
}

::-webkit-scrollbar-track {
  background-color: var(--color-bg-dark);
}

::-webkit-scrollbar-thumb {
  background-color: var(--color-scrollbar);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-accent);
}